.already-av-holder {
	border-radius: 50%;
	height: 100px;
	width: 100px;
	border: 1px solid #ccc;
	margin: 0 auto;

}

.already-av-img {
	margin: 0 auto;
	width: 71px;
	height: 100px;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	display: -ms-flexbox;
	display: flex;
}

.already-av-text {
	text-align: center;
	font-weight: bold;
	color: #000;
	margin-top: 20px;
	font-size: 18px;
}
.flexClass {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}
.alread-av-desc {
	text-align: center;
	color: #4e4e55;
	margin-bottom: 40px;
    margin: 30px auto 45px;
    width: 80%;
}
.alreadydiv {
	width: 81%;
	-ms-flex-pack: center;
	    justify-content: center;
	margin: 0 auto;
}
.already-select-btn {
	background-color: #413ae2;
	color: #ffffff;
	width: 81%;
	padding: 8px 10px;
	border-radius: 5px;
	text-align: center;
	border: 0px;
	font-family: 'Aeonik';
}
.already-select-btn:focus{
	outline: none;
}
.backArrow{
	position:absolute !important;
	top:20px;
	left:20px;
}


