body{
    background-color:#FCFCFD;
}



@media screen and (min-width:1200px){
    .container{
        max-width:1265px;
    }
}
.event-hero-sidebar
{
    margin-top: 32px;
}
@media screen and (min-width: 1100px) {
	.event-image {
		height: 29vw !important;
		width: 100%;
		-o-object-fit: fill;
		   object-fit: fill;
	}
}
.event-image {
	/* width: 370px; */
	height: 350px;
	-o-object-fit: fill;
	   object-fit: fill;
    border-radius: 8px;
}
.iconHolder{
	width:50px;
	height: 50px;
	border-radius: 50%;
	background: -webkit-gradient(linear, right top, left top, from(rgba(94, 91, 255, 0.12)), color-stop(131.25%, rgba(124, 118, 255, 0)));
	background: -webkit-linear-gradient(right, rgba(94, 91, 255, 0.12) 0%, rgba(124, 118, 255, 0) 131.25%);
	background: -o-linear-gradient(right, rgba(94, 91, 255, 0.12) 0%, rgba(124, 118, 255, 0) 131.25%);
	background: linear-gradient(270deg, rgba(94, 91, 255, 0.12) 0%, rgba(124, 118, 255, 0) 131.25%);;
	-ms-flex-pack: center;;
	    justify-content: center;
    -ms-flex-align: center;
        align-items: center;
   display: -ms-flexbox;
   display: flex;
   margin-left:11px;
   border: none;

}
.iconHolder i{
    font-size: 23px;
}
@media screen and (max-width: 600px) {
.header2{
    display: block;
}
.iconHolder i{
    font-size: 16px;
}
.iconHolder {
    width:40px;
    height: 40px;
    display: inline;
    margin-bottom: 5px;
}
}



